  computed: {},
  filters: {
    lineStatusFilte(value){
        if(value==1){
          return"未处理"
        }else{
          return "已处理"
        }
      },
    TimeFilte(value){
      if(value.indexOf('T')>-1){
       return value.replace(/T/g," ");
      }else{
        return value;
      }
    },
    ExpressFilte(value){//快递类型
      if(value==1){
        return "快递物流"
      }else if(value==2){
        return "自提"
      }
    },
    OrderFilte(value){//订单类型
      if(value==1){
        return "订货单"
      }else if(value==2){
        return "退货单"
      }else if(value==3){
        return "加急单"
      }
    },
    formatTrim(value) {
      //去除空格  input组件中会报错
      return value.replace(/\s+/g, "");
    },
    formatNull(value) {

      if (value === undefined || value === null || value === "") {
        return "暂无";
      } else {
          if(value.indexOf('$')>-1){
          value=value.split('$')[1];
        }
        return value;
      }
    },
    getPercentage(value) {
      return Math.round(value * 100 * 1000) / 1000 / 100;
    },
    formatPhone(value) {
      return (value = value.replace(/\D/g, ""));
    },
    formatLongText(value) {
      if (value === undefined || value === null || value === "") {
        return "暂无";
      } else if (value.length > 8) {
        return value.substr(0, 8) + "...";
      } else {
        return value;
      }
    },
    formatStatus(value) {
      //判断  当前订单头状态 (0.未确认；1.已完成；3.待接单；4.已接单；5.已取消)
      if (value === 0) {
        return "未确认";
      } else if (value === 1) {
        return "已完成";
      } else if (value === 2) {
        return "待审核";
      } else if (value === 3) {
        return "待接单";
      } else if (value === 4) {
        return "已接单";
      } else {
        return "已取消";
      }
    },
    formatLineStatus(value) {
      //判断  当前订单行状态  (0.未派发；1.待接单；2.已接单；3.已取消；）
      if (value === 0) {
        return "未派发";
      } else if (value === 1) {
        return "待接单";
      } else if (value === 2) {
        return "已接单";
      } else {
        return "已取消";
      }
    },
    formatProductAttr(value) {
      if (value == null) {
        return "";
      } else {
        let attr = JSON.parse(value);
        let result = "";
        for (let i = 0; i < attr.length; i++) {
          result += attr[i].key;
          result += ":";
          result += attr[i].value;
          result += ";";
        }
        return result;
      }
    }
  },
  methods: {
    getOrderDetail() {
      //所有的商品信息
      this.listLoading = true;
      this.$request.GetOrderDetails(this.OrderNo).then(response => {
        this.listLoading = false;
        let content = response.content;
         this.isStatus=false;//是否可以接单
         this.accomplish=false;//是否完成
        for (let i = 0; i < content.length; i++) {
          content[i].maxVlaue = content[i].GoodsCount;
          if(content[i].Status==1){
            this.isStatus=true;
          }
          if(content[i].Status==2){
              this.accomplish=true;
          }
           let GoodsValueList=content[i].GoodsValuesList;
            let MappingList=JSON.parse(JSON.stringify(this.GoodsValueMappingList));
            GoodsValueList.forEach(item=>{
                let {CategoryType,GoodsValueName,SerialNumber}=item;
                  MappingList[CategoryType-1].GoodsValueList.push({GoodsValueName,SerialNumber})
            })
            for(let x=0;x<MappingList.length;x++){
              if(MappingList[x].GoodsValueList.length==0){
                MappingList.splice(x,1)
                x--;
              }
            }
          content[i].MappingList=MappingList;


        }
        this.ordList = content;
        this.calcTotalPrice();
        /*this.ordList.forEach(item =>{
            this.totleMoney += item.GoodsCount* item.SpecialOffer; //商品总计价格
          });*/
      });
    },
    onInfoMess() {
      //获取收货人的信息 --- 查询到之前的接口
      var BeginTime = this.ct;
      var EndTime = formatDateAdd(CreateTime(this.ct), 1);
      var SupplierID=JSON.parse(localStorage.getItem('userInfoS')).SuppliersID;
      var info = {
        Offset: 0,
        Rows: 10,
        KeyWords: "",
        BeginTime: BeginTime,
        EndTime: EndTime,
        Status: "",
        OrderNo: this.OrderNo,
        SupplierID:SupplierID,
        UserID: "",
        OrderType:null,
      };

      this.$request.GetListOrderforSuppier(info).then(response => {
        let content=response.content;
            content.forEach(item=>{
              // Status1: 3
              // Status2: 1
              // Status3: 3
              if(item.Status1>=2&&item.Status2>=2&&item.Status3>=2){
                    item.lineStatus=2;
              }else{
                 item.lineStatus=1;
              }
            })
        this.infoList = content[0];
        if(!this.infoList.PickupTime){
          this.HeadSpan=8;
        }
        this.tableData = content.slice(0,1);
        this.Status = this.infoList.Status;
        // console.log(this.infoList);
        this.orderNote = this.infoList.Notes;
        this.markInfo.note = this.orderNote;
        // console.log(response)
      });
    },
    onSelectRegion(data) {
      //修改收货人信息内 获取省 市 县
      this.receiverInfo.receiverProvince = data.province.value;
      this.receiverInfo.receiverCity = data.city.value;
      this.receiverInfo.receiverRegion = data.area.value;
    },
    calcTotalPrice: function() {
      //计算总价
      var _this = this;
      _this.totleMoney = 0;
      _this.supplierCount = 0;
      _this.allCount=0;
      let expDate = []; //
      this.ordList.forEach(function(item, index) {
        // if(item.checked){
        let itemCount = item.DeliverCount;
        if (!itemCount && itemCount !== 0) {
          itemCount = 1;
        }
        _this.allCount+=itemCount;
        _this.totleMoney += itemCount * item.SupplyPrice;
        expDate.push(item.DefultSupplier);
        // }
      });
      this.supplierCount = Array.from(new Set(expDate)).length;
    },
    handleChange(value) {
      //编辑数字改变
      this.calcTotalPrice();
    },
    HandleBlur(row) {
      if (!row.DeliverCount&&row.DeliverCount!=0) {
        this.$nextTick(() => {
          row.DeliverCount = 1;
        });
      }
