    formatTime(time) {
      if (time == null || time === "") {
        return "";
      }
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd");
    },
    formatStepStatus(value) {
      //辨别步骤条进展  (0.未确认；1.已完成；3.待接单；4.已接单；5.已取消)
      // console.log(value)
      if (value === 0) {
        //未确认
        return 1;
      } else if (value === 1) {
        //已完成
        return 4;
      } else if (value === 2) {
        //已审核  去除
        return 2;
      } else if (value === 3) {
        //待接单
        return 2;
      } else if (value === 4) {
        //已接单
        return 3;
      } else {
        //已取消
        return 4;
      }
    },
    showUpdateReceiverDialog() {
      //点击打开 修改收货人信息
      var shen = this.infoList.RcAddr.indexOf("省"); //倒推显示 省市县区
      var shi = this.infoList.RcAddr.indexOf("市");
      var xian = this.infoList.RcAddr.indexOf("县");
      var qu = this.infoList.RcAddr.indexOf("区");
      // console.log("this.receiverInfo.RcAddr"+this.infoList.RcAddr)
      // console.log(shen,shi,xian,qu)
      this.receiverInfo.RcAddr = this.infoList.RcAddr;
      if (shen != -1 && this.infoList.RcAddr.slice(0, shen + 1) != "省") {
        this.receiverInfo.receiverProvince = this.receiverInfo.RcAddr.slice(
          0,
          shen + 1
        );
        if (
          shi != -1 &&
          this.infoList.RcAddr.slice(shen + 1, shi + 1) != "市"
        ) {
          this.receiverInfo.receiverCity = this.receiverInfo.RcAddr.slice(
            shen + 1,
            shi + 1
          );
          if (
            xian != -1 &&
            this.infoList.RcAddr.slice(shi + 1, xian + 1) != "县"
          ) {
            this.receiverInfo.receiverRegion = this.receiverInfo.RcAddr.slice(
              shi + 1,
              xian + 1
            );
            this.receiverInfo.RcAddr = this.receiverInfo.RcAddr.slice(xian + 1);
            // console.log("县:"+this.receiverInfo.RcAddr )
          } else if (
            qu != -1 &&
            this.infoList.RcAddr.slice(shi + 1, qu + 1) != "区"
          ) {
            this.receiverInfo.receiverRegion = this.receiverInfo.RcAddr.slice(
              shi + 1,
              qu + 1
            );
            this.receiverInfo.RcAddr = this.receiverInfo.RcAddr.slice(qu + 1);
            // console.log("区:"+this.receiverInfo.RcAddr )
          }
        }
      }
      // console.log("this.receiverInfo.RcAddr"+this.receiverInfo.RcAddr)

      // console.log(this.receiverInfo)
      this.receiverDialogVisible = true;
      this.receiverInfo.OrderNo = this.infoList.OrderNo;
      this.receiverInfo.RcName = this.infoList.RcName;
      this.receiverInfo.RcPhone = this.infoList.RcPhone;
      // receiverPostCode:,
