    handleUpdateReceiverInfo() {
      //确定 修改收货人信息
      this.$confirm("是否要修改收货信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        //检验填写的信息都不能为空
        // console.log(this.receiverInfo)
        if (
          this.receiverInfo.RcName == "" ||
          this.receiverInfo.RcPhone == "" ||
          this.receiverInfo.RcAddr == ""
        ) {
          this.$message({
            message: "必须将信息填写完整",
            type: "warning",
            duration: 2000
          });
          return;
        }
        //检测电话号码是否规则
        if (!/^1[345678]\d{9}$/.test(this.receiverInfo.RcPhone)) {
          //if (!/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/.test($("#tel").val())) {
          this.$message({
            message: "请输入合法电话号码",
            type: "warning",
            duration: 2000
          });
          return;
        }

        this.receiverInfo.linAddr = "";
        if (
          this.receiverInfo.receiverProvince != null &&
          this.receiverInfo.receiverProvince != "省"
        ) {
          //有选择市的可以拼接
          this.receiverInfo.linAddr += this.receiverInfo.receiverProvince;
        }
        if (
          this.receiverInfo.receiverCity != null &&
          this.receiverInfo.receiverProvince != "市"
        ) {
          //
          this.receiverInfo.linAddr += this.receiverInfo.receiverCity;
        }
        if (
          this.receiverInfo.receiverRegion != null &&
          this.receiverInfo.receiverProvince != "县" &&
          this.receiverInfo.receiverProvince != "区"
        ) {
          this.receiverInfo.linAddr += this.receiverInfo.receiverRegion;
        }
        this.receiverInfo.RcAddr =
          this.receiverInfo.linAddr + this.receiverInfo.RcAddr;

        // console.log(this.receiverInfo)   //关于个人信息的传参
        editReceiptInfo(this.receiverInfo).then(response => {
          this.receiverDialogVisible = false;
          this.$message({
            type: "success",
            message: "修改成功!"
          });
          // console.log(response)
          this.onInfoMess(); //更新收货信息
        });
      });
    },
    updateStatus(val) {
      //更改状态  改完之后要重新请求步骤进展   已完成/已取消的不能再更改状态
      if (!this.editCount) {
        this.$confirm("此操作将不保存编辑内容, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        });
        return;
      }
      // 取消订单的传参val不会改变 //(0.未确认；1.已完成；3.待接单；4.已接单；5.已取消)  已审核2去除了
      //(0.未派发；1.待接单；2.已接单；3.已取消；） 订单行的状态-->
        // if (val == 0) {
        //   val = 3;
        // }else if (val == 3) {
        //   val = 2;
        // } else if (val == 4) {
        //   val = 1;
        // }

      let editStatus = [];
      for (let i = 0; i < this.ordList.length; i++) {
        let { Orders_Lines_ID,Notes,Status } = this.ordList[i];
        if(Status!=1)continue;
      editStatus.push({ OrderLineID: Orders_Lines_ID, Status:val,OrderNo:this.OrderNo,Notes});
      }
      this.updateStatusRequest(editStatus);
    },
