    // oneExportClick(list) {
    //   const jsonData = list;
    //   //列标题，逗号隔开，每一个逗号就是隔开一个单元格
    //   let str = `货号,商品名,属性1,属性2,属性3,属性4,属性5,备注,数量,状态\n`;
    //   //增加\t为了不让表格显示科学计数法或者其他格式
    //   for (let i = 0; i < jsonData.length; i++) {
    //     for (let item in jsonData[i]) {
    //       str += `${jsonData[i][item] + "\t"},`;
    //     }
    //     str += "\n";
    //   }
    //   //encodeURIComponent解决中文乱码
    //   let uri = "data:text/csv;charset=utf-8,\ufeff" + encodeURIComponent(str);
    //   //通过创建a标签实现
    //   let link = document.createElement("a");
    //   link.href = uri;
    //   //对下载的文件命名
    //   link.download = "商品信息.csv";
    //   document.body.appendChild(link);
    //   link.click();
    //   document.body.removeChild(link);
    // }
 excelExp() {
      //导出excel
      // if (this.multipleSelection.length === 0) {
      //   this.$message({
      //     message: "请至少勾选一项，再进行操作",
      //     type: "warning"
      //   });
      // } else {
        // let expDate = [];
        // this.multipleSelection.forEach(item => {
        //   expDate.push({
        //     GoodsID: item.GoodsID, //货号
        //     GoodsName: item.GoodsName, //
        //     CategoryName1: item.CategoryName1,
        //     CategoryName2: item.CategoryName2,
        //     CategoryName3: item.CategoryName3,
        //     CategoryName4: item.CategoryName4,
        //     CategoryName5: item.CategoryName5,
        //     SuppliersName: item.SuppliersName, //供应商名
        //     SupplierPhone: item.SupplierPhone, //供应商电话
        //     // SupplyPrice:item.SupplyPrice,
        //     GoodsCount: item.GoodsCount,
        //     // SpecialOffer: item.SpecialOffer, //价格
        //     Status: item.Status
        //   });
        // });
        // this.oneExportClick(expDate);
        let obj=[];
        // trHead='<tr><td>订单ID</td><td>默认供货商</td><td>供应商电话</td><td>商品名</td><td>商品数量</td><td>味道</td><td>颜色</td><td>尺寸</td><td>行备注</td></tr>';
        this.ordList.forEach((item,i)=>{
          let ValueName='';
          let Numbers='';
          for(let i=0;i<item.GoodsValuesList.length;i++){
              let {GoodsValueName,SerialNumber}=item.GoodsValuesList[i];
              ValueName+=GoodsValueName+'<br/>'
              Numbers+=SerialNumber+'<br/>'
          }
          if(!item.Notes){
            item.Notes='';
          }
          obj.push({
            // Orders_Lines_ID:item.Orders_Lines_ID,
            index:i,
            SupplyPrice:item.SupplyPrice,
            GoodsName:item.GoodsName,
            GoodsCount:item.GoodsCount,
            DeliverCount:item.DeliverCount,
            NOrefund:(item.GoodsCount-item.DeliverCount)*item.SupplyPrice,
            refund:item.DeliverCount*item.SupplyPrice,
            // CategoryName1:item.CategoryName1,
            // CategoryName2:item.CategoryName2,
            // CategoryName3:item.CategoryName3,
            // SpecialOffer:item.SpecialOffer,//平台售价
            // OriginalPrice:item.OriginalPrice,//零售价
            Notes:item.Notes,
            ValueName:ValueName,
            Numbers:Numbers,
          })
        });
      // trHead='<tr><td>订单编号</td><td>收货人</td><td>收货地址</td><td>收货人电话</td><td>订单备注</td><td>订单类型</td><td>快递类型</td></tr><td>商品数量</td>';

      // trHead='<tr><td>订单编号</td><td>自提人</td><td>自提人电话</td><td>自提人车牌号</td><td>自提时间</td><td>订单备注</td><td>订单类型</td><td>快递类型</td></tr><td>商品数量</td>';
      let HeadObj=[];
      for(let i=0;i<this.tableData.length;i++){
        let obj=Object.assign({},this.tableData[i]);
        let {Out_Trade_No,RcName,RcAddr,RcPhone,PickUpMan,PickUpPhone,CarNumber,PickupTime,Notes:Head_Notes,OrderType,ExpressType,DeliverCount}=obj;

        if(OrderType==1){
          obj.OrderTypeitem="订货单"
        }else if(OrderType==2){
          obj.OrderTypeitem="退货单"
        }else if(OrderType==3){
          obj.OrderTypeitem="加急单"
        }
        if(Head_Notes&&Head_Notes.indexOf('$')>-1){
          Head_Notes=Head_Notes.split('$')[1]
        }else{
          Head_Notes='';
        }

        if(obj.ExpressType==1){
          obj.ExpressTypeitem='快递物流';
        }else if(obj.ExpressType==2){
          obj.ExpressTypeitem='自提';
        }else{
          obj.ExpressTypeitem='';
        }
        if(!PickupTime){
          PickupTime="";
        }
        HeadObj.push({
            Out_Trade_No,PickupTime,Head_Notes,OrderTypeitem:obj.OrderTypeitem,ExpressTypeitem:obj.ExpressTypeitem,DeliverCount
          })
      }
       let ALL = [{ refundCOUNT:0,allTotal: 0, refundPrice:0,allPrice: 0 }];
        obj.forEach(item => {
           ALL[0].refundCOUNT += (item.GoodsCount-item.DeliverCount);
          ALL[0].allTotal += item.DeliverCount;
          ALL[0].refundPrice += ( item.GoodsCount-item.DeliverCount) * item.SupplyPrice;
          ALL[0].allPrice += item.DeliverCount * item.SupplyPrice;
        });
      let line=this.getTbale(3,obj);
      let Head=this.getTbale(1,HeadObj);
       let sum = this.getTbale(4, ALL);
        this.oneExportClick([Head,line,sum])
      // }
    },
    getTbale(str,list){//将数据转换成table
    let trHead=''
    if(str==1){
      trHead='<tr><td>订单编号</td><td>自提时间</td><td>订单备注</td><td>订单类型</td><td>快递类型</td><td>退货数量</td></tr>';
    }else if(str==3){
      trHead='<tr><td>序列号</td><td>价格</td><td>商品名</td><td>订单数量</td><td>退货数量</td><td>未退价格</td><td>退货价格</td><td>行备注</td><td>属性</td><td>条码</td></tr>';
    }else if (str == 4) {
        trHead = "<tr><td>未退件数</td><td>退货件数</td><td>未退合计价格</td><td>退货合计价格</td></tr>";
      }
      for(let i=0;i<list.length;i++){
        trHead+='<tr>';
          let obj=list[i];
          for(let item in obj){
              trHead+=`<td>${obj[item]+'\t'}</td>`;
          }
      }
      trHead+='</tr>';
      return trHead;
    },
    /**
     * 导出的列表数据
     */
    oneExportClick(list){
       let worksheet = 'Sheet1'
      let uri = 'data:application/vnd.ms-excel;base64,';
      let table='';
      for(let i=0;i<list.length;i++){
        table+=`<table>${list[i]}</table><br/>`
      }
      //下载的表格模板数据
      let template = `<html xmlns:o="urn:schemas-microsoft-com:office:office"
      xmlns:x="urn:schemas-microsoft-com:office:excel"
      xmlns="http://www.w3.org/TR/REC-html40">
      <head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet>
        <x:Name>${worksheet}</x:Name>
        <x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet>
        </x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]-->
        <title>订单</title>
        </head><body>${table}</body></html>`;
      //下载模板
      // console.log(uri + this.base64(template));
      window.location.href = uri + this.base64(template)
    },
    base64(s){return window.btoa(unescape(encodeURIComponent(s))) },
  }
};
</script>
<style scoped>
.detail-container {
  /*width: 80%;*/
  padding: 20px 20px 20px 20px;
  margin: 20px auto;
}

.operate-container {
  background: #f2f6fc;
  height: 80px;
  margin: -20px -20px 0;
  line-height: 80px;
}

.operate-button-container {
  float: right;
  margin-right: 20px;
}

.table-layout {
  margin-top: 20px;
  border-left: 1px solid #dcdfe6;
  border-top: 1px solid #dcdfe6;
}

.table-cell {
  height: 60px;
  line-height: 40px;
  border-right: 1px solid #dcdfe6;
  border-bottom: 1px solid #dcdfe6;
  padding: 10px;
  font-size: 14px;
  color: #606266;
  text-align: center;
  overflow: hidden;
}

.table-cell-title {
  border-right: 1px solid #dcdfe6;
  border-bottom: 1px solid #dcdfe6;
  padding: 10px;
  background: #f2f6fc;
  text-align: center;
  font-size: 14px;
  color: #303133;
}
.padT {
  padding-right: 20px;
}
.el-table-column p {
  overflow: auto;
  height: auto;
  min-height: 60px;
}
.el-table-column .el-button--primary {
  margin: auto;
  display: block;
}
</style>





